body {
  background: url("http://freestock.ca/black_stone_texture_sjpg3692.jpg") no-repeat center center fixed;
  background-size: cover;
  font-family: "Ubuntu Mono", monospace; }

.inline {
  display: inline-block;
  vertical-align: top; }

.container {
  margin: 0px auto 0px auto;
  text-align: center; }

h1 {
  color: #858580;
  text-shadow: 0 0 15px black; }

p {
  margin: 0 5px 15px 0;
  padding: 3px;
  color: #858580;
  background-color: #2F2A27;
  border-radius: 3px;
  -webkit-box-shadow: 0 0 3px #111;
          box-shadow: 0 0 3px #111; }

#displays {
  width: 760px;
  margin-left: auto;
  margin-right: auto;
  background-color: #2F2A27;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 15px #000;
          box-shadow: 0 0 15px #000; }

#display-canvas {
  margin: 5px 0px 5px 0px; }

#console-canvas {
  margin: 0px 0px 5px 0px; }



